.intro {
  width: 100%;
  height: 100vh;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 3rem;
  background-color: rgb(0, 1, 14);
  color: white;

  img {
    width: 23rem;
    height: 20rem;
    transform: rotate(-4deg);
  }
  .me {
    margin: 0 4rem;
    h1 {
      font-size: 30px;
      font-family: 'Poppins';
      margin: 1.5rem 0;
      span {
        color: rgb(255, 0, 106);
        font-size: 30px;
      }
    }
    p {
      margin: 0.8rem 0;
      font-family: Arial, Helvetica, sans-serif;

      span {
        color: rgb(255, 0, 106);
      }
    }
  }
}

@media (max-width: 768px) {
  .intro {
    flex-direction: column;
    padding: 20rem 1.5rem;

    img {
      width: 100%;
    }
    .me {
      margin: 1rem 0;
      h1 {
        font-size: 18px;
        span {
          font-size: 19px;
        }
      }
      p {
        font-size: 15px;
        span {
          font-size: 15px;
        }
      }
    }
  }
}
